body {
  background-color: #282c34;
  color: white;
  padding: 40px;
  font-family: Arial;
}


a {
    color:#4B99AD;
    text-decoration: none;
}


a:hover {
    color:#686868;

}

img {
    display: block;
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  border-radius: 2%;
  border: 1px solid;
  border-color: grey;
  margin-bottom: 4%;

}


hr {
    display: block;
    height: 1px;
    border: 0;
    border-top: 1px solid grey;
    margin: 1em 0;
    padding: 0;
    margin-top: 7%;
    margin-bottom: 7%;
}


.image-margin-bottom {
    margin-bottom: 10%
}

.main-header-center {
  background-color: #282c34;
  font-size: 30px;
  color: white;
  text-align: center;
}


.main-header-left {
  background-color: #282c34;
  font-size: 25px;
  color: white;
  text-align: left;
  margin-bottom: 25px
}


.space-right{
    margin-right: 10px;
}

.brown-color {
    background: #D2691E !important;

}

.grey-color {
    background: #404040 !important;
}



.grey-color:hover{
	background: #686868 !important;
	box-shadow:none !important;
	-moz-box-shadow:none !important;
	-webkit-box-shadow:none !important;
}

.brown-color:hover{
	background: #785027 !important;
	box-shadow:none !important;
	-moz-box-shadow:none !important;
	-webkit-box-shadow:none !important;
}


.form-style-1 {
	margin:10px auto;
	max-width: 400px;
	padding: 20px 12px 10px 20px;
	font: 13px "Lucida Sans Unicode", "Lucida Grande", sans-serif;

}

.details-style-1 {
	margin:10px auto;
	max-width: 400px;
	padding: 20px 12px 10px 20px;
	font: 16px "Lucida Sans Unicode", "Lucida Grande", sans-serif;
}

.details-style-1 li {
	padding: 0;
	list-style: none;
	margin: 10px 0 0 0;
}

.details-style-1 label{
	margin:0 0 3px 0;
	padding:0px;
	font-weight: bold;
}

.details-style-1 input[type=submit], .form-style-1 input[type=button]{
	background: #4B99AD;
	padding: 8px 15px 8px 15px;
	border: none;
	color: #fff;
    margin-top: 19px;
    border-radius: 10%;
}

.details-style-1 input[type=submit]:hover, .form-style-1 input[type=button]:hover{
	background: #668B8B;
	box-shadow:none;
	-moz-box-shadow:none;
	-webkit-box-shadow:none;
    cursor: pointer;
}




.form-style-1 li {
	padding: 0;
	display: block;
	list-style: none;
	margin: 10px 0 0 0;


}
.form-style-1 label{
	margin:0 0 3px 0;
	padding:0px;
	display:block;
	font-weight: bold;

}
.form-style-1 input[type=text],
.form-style-1 input[type=date],
.form-style-1 input[type=datetime],
.form-style-1 input[type=number],
.form-style-1 input[type=search],
.form-style-1 input[type=time],
.form-style-1 input[type=url],
.form-style-1 input[type=email],
textarea,
select{
	box-sizing: border-box;
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	border:1px solid #BEBEBE;
	padding: 7px;
	margin:0px;
	-webkit-transition: all 0.30s ease-in-out;
	-moz-transition: all 0.30s ease-in-out;
	-ms-transition: all 0.30s ease-in-out;
	-o-transition: all 0.30s ease-in-out;
	outline: none;
}
.form-style-1 input[type=text]:focus,
.form-style-1 input[type=date]:focus,
.form-style-1 input[type=datetime]:focus,
.form-style-1 input[type=number]:focus,
.form-style-1 input[type=search]:focus,
.form-style-1 input[type=time]:focus,
.form-style-1 input[type=url]:focus,
.form-style-1 input[type=email]:focus,
.form-style-1 textarea:focus,
.form-style-1 select:focus{
	-moz-box-shadow: 0 0 8px #88D5E9;
	-webkit-box-shadow: 0 0 8px #88D5E9;
	box-shadow: 0 0 8px #88D5E9;
	border: 1px solid #88D5E9;

}
.form-style-1 .field-divided{
	width: 49%;
}

.form-style-1 .field-long{
	width: 100%;
}

.form-style-1 .space-bottom{
	margin-top: 100px;
}

.form-style-1 .field-select{
	width: 100%;
}
.form-style-1 .field-textarea{
	height: 100px;
}
.form-style-1 input[type=submit], .form-style-1 input[type=button]{
	background: #4B99AD;
	padding: 8px 15px 8px 15px;
	border: none;
	color: #fff;
    border-radius: 10%;
}
.form-style-1 input[type=submit]:hover, .form-style-1 input[type=button]:hover{
	background: #668B8B;
	box-shadow:none;
	-moz-box-shadow:none;
	-webkit-box-shadow:none;
    cursor: pointer;
}
.form-style-1 .required{
	color:red;
}
